<template>
  <div>
    <el-dialog
      :visible.sync="showDlg"
      :show-close="false"
      custom-class="edit-dialog"
      @open="onDlgOpen"
    >
      <el-steps :active="0" finish-status="success" simple>
        <el-step title="安全验证" icon="el-icon-lock" />
        <el-step title="修改密码" icon="el-icon-edit" />
        <el-step title="修改成功" icon="el-icon-check" />
      </el-steps>
      <div class="h-item">
        <span class="title">验证方式</span>
        <el-input v-model="account" style="width:500px;">
          <el-select slot="prepend" v-model="codeFrom" style="width:120px;" @change="onCodeFromChange">
            <el-option label="手机" value="sms" />
            <el-option label="邮箱" value="email" />
          </el-select>
        </el-input>
      </div>
      <div class="h-item" style="margin-top:30px; margin-bottom:30px">
        <span class="title">验证码</span>
        <el-input v-model="vcode" placeholder="请输入验证码" clearable style="width:500px;">
          <el-button slot="append">获取验证码</el-button>
        </el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="medium" @click="closeDialog">取 消</el-button>
        <el-button size="medium" type="primary" @click="closeDialog">下一步</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showDlg: false,
      codeFrom: '',
      account: '',
      vcode: ''
    }
  },
  methods: {
    openDialog() {
      this.showDlg = true
    },
    closeDialog() {
      this.showDlg = false
    },
    onDlgOpen() {
      this.codeFrom = 'sms'
      this.account = '13581979982'
    },
    onCodeFromChange(value) {
      if (value === 'sms') {
        this.account = '13581979982'
      } else if (value === 'email') {
        this.account = 'admin@runqiaoinfo.com'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .edit-dialog {
    width: 800px;
    white-space: nowrap;
    .h-item {
      height: 32px;
      width: 600px;
      white-space: nowrap;
      .title {
        width: 4em;
        margin: 2em;
        font-size: 16px;
        display: inline-block;
      }
    }
  }

</style>
