<template>
  <div class="page-container">
    <el-card class="safety-card">
      <div slot="header" class="fixheader">
        <span>安全等级</span>
      </div>
      <div class="score-wrapper">
        <el-progress
          :percentage="score"
          :stroke-width="16"
          :color="scoreColors"
          :show-text="false"
          class="score-bar"
        />
        <span class="score-text" :style="{'color':scoreColors}">{{ score }}</span>
        <span> 分</span>
      </div>
      <div class="content-wrapper">
        <div class="item">
          <i class="el-icon-lock icon" />
          <span class="title">账户密码</span>
          <span class="value">上次更改时间：2020-12-08</span>
          <el-button class="button" type="info" plain size="medium" @click="onChangePassword">修改密码</el-button>
        </div>
        <div class="item">
          <i class="el-icon-mobile-phone icon" />
          <span class="title">绑定手机</span>
          <span class="value">{{ mobilePhone }}</span>
          <el-button class="button" type="info" plain size="medium" @click="onBindPhone">修改</el-button>
        </div>
        <div class="item">
          <i class="el-icon-message icon" />
          <span class="title">绑定邮箱</span>
          <span class="value">s***t@runqiaoinfo.com</span>
          <el-button class="button" type="info" plain size="medium" @click="onBindEmail">修改</el-button>
        </div>
        <div class="item">
          <i class="el-icon-mobile icon" />
          <span class="title">手机安全验证</span>
          <span class="value">已开启</span>
          <el-button class="button" type="info" plain size="medium" @click="onTogglePhoneVeri">关闭</el-button>
        </div>
      </div>
    </el-card>

    <change-password-dialog ref="changePsw" />

  </div>
</template>

<script>
import { ChangePasswordDialog } from './components'
import { mapGetters } from 'vuex'

export default {
  name: 'Safety',
  components: {
    ChangePasswordDialog
  },
  data() {
    return {
      score: 90,
      scoreColors: [
        { color: '#f56c6c', percentage: 20 },
        { color: '#909399', percentage: 40 },
        { color: '#e6a23c', percentage: 60 },
        { color: '#1989fa', percentage: 80 },
        { color: '#5cb87a', percentage: 100 }
      ]
    }
  },
  computed: {
    ...mapGetters({
      userId: 'user/userId',
      userName: 'user/userName',
      mobilePhone: 'user/mobilePhone',
      email: 'user/email',
      ugroup: 'user/ugroup',
      cid: 'user/cid',
      uscc: 'user/uscc'
    })
  },
  methods: {
    onChangePassword() {
      this.$refs.changePsw && this.$refs.changePsw.openDialog()
    }
  }
}
</script>

<style lang="scss" scoped>
  .safety-card {
    font-family: 'Microsoft YaHei';
    font-size: 16px;
    white-space: nowrap;
    .fixheader:after {
      display: table;
      content: "";
    }
    .fixheader:after {
      clear: both
    }
    .fixheader {
      padding: 5px;
    }
    .score-wrapper {
      padding: 5px;
      .score-bar {
        width: 560px;
        display: inline-block;
      }
      .score-text {
        margin-left: 25px;
        font-size: 38px;
        display: inline-block;
      }
    }
    .content-wrapper {
      padding: 10px;
      .item {
        margin-top: 20px;
        .icon {
          width: 32px;
          font-size: 15px;
          display: inline-block;
        }
        .title {
          width: 160px;
          display: inline-block;
        }
        .value {
          width: 300px;
          display: inline-block;
        }
        .button {
          width: 100px;
          text-align: center;
          display: inline-block;
        }
      }
    }
  }

</style>
